<template>
  <div class="wallets-container">
    <div v-show="showWallets">
      <h3 class="text-center">Wallets</h3>
      <div class="button-container">
        <button
          v-touch:tap="touchMobbyTokenWallet"
          class="mid-round-button-blue-filled"
        >
          Mobby
        </button>
        <button
          v-touch:tap="touchEthWallet"
          class="mid-round-button-green-filled"
        >
          Ethereum
        </button>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
export default {
  data() {
    return {
      showWallets: true
    };
  },
  mounted() {
    this.showWallets = true;
  },
  methods: {
    touchEthWallet() {
      this.showWallets = false;
      this.$router.push('/wallets/eth');
    },
    touchMobbyTokenWallet() {
      this.showWallets = false;
      this.$router.push('/wallets/mobby');
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'WalletLayout.scss';
</style>
